<template>
	<div id="book">
		<banner></banner>
		<!-- {{list}} -->
		<div class="bookCont">
			<div class="bookContLeft">
				<div style="border-bottom: 1px solid #ccc;height: 240px;">
					<img style="width: 203px;height: 203px;float: left;" src="https://ccdn.goodq.top/caches/628d236aceaa012698bd7fe4b8dc188c/aHR0cDovLzU4NzQ1YTE5NzJjNmYudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxNy8wNC84NmViYjQ5YjE1NWU2ZGY0MGFlODAxZmM3ODc0MWY1MC5qcGc_p_p100_p_3D.jpg" alt="">
					<div style="float: left;width: 300px;height: 203px;padding-left: 20px;">
						<p>{{list.name}}</p>
						<p style="color: #e04f67;">¥{{list.money}}</p>
						<div class="yudingBtn">
							<a href="#/bar" class="yudingTo" @click="toBar">在线预订</a>
							<span @click="nn=nn--<=1?1:nn--" style="border: 1px solid #e04f67;display: inline-block;width: 25px;height: 25px;text-align: center;line-height: 25px;cursor: pointer;">-</span>
							<input type="text" style="width: 28px;height: 25px;position: relative;top:-1px;text-align: center;" value="1" v-model="nn">
							<span @click="nn++" style="border: 1px solid #e04f67;display: inline-block;width: 25px;height: 25px;text-align: center;line-height: 25px;cursor: pointer;">+</span>
						</div>
						<p style="margin-top: 25px;font-size: 14px;color: #999;">预付旅游订金</p>
					</div>
				</div>
				<div class="introduce" style="border-left: 3px solid #E04F67;margin-top: 30px;text-indent: 7px;font-size: 18px;">景点介绍 <span style="color:#ccc;font-size: 14px;">/ introduce</span></div>
				<div class="introduceTxt" style="margin-left: 10px;font-size: 12px;color: #999;line-height: 22px;margin-top: 30px;">
					卢浮宫位于法国巴黎市中心的塞纳河北岸，位居世界四大博物馆之首。 卢浮宫始建于1204年，原是法国的王宫，居住过50位法国国王和王后，是法国文艺复兴时期最珍贵的建筑物之一，以收藏丰富的古典绘画和雕刻而闻名于世。
				</div>
			</div>
			<div class="bookContRight">
				
			</div>
		</div>
	</div>
</template>

<script>
	import banner from '../parts/banner.vue'
	import Bus from '../parts/bus.js'
	import {mapGetters,mapActions} from 'vuex'
	var itemD;
	Bus.$on('itemData',(data)=>{
		itemD=data;
	})
	export default{
		components:{
			banner
		},
		data(){
			return{
				list:itemD,
				nn:1
			}
		},
		created(){
			Bus.$on('itemData',(data)=>{
				this.list=data;
			})
		},
		methods:{
			toBar(){
				Bus.$emit('chanPA', 10)
				this.list['itemNNN']=parseInt(this.nn);
				Bus.$emit('pushItem',this.list)
				console.log(this.list);
				this.nn=1;
			}
		}
	}
</script>

<style>
	.yudingTo{
		display: inline-block;
		background-color: rgb(224, 79, 103);
		width: 168px;
		height: 44px;
		text-align: center;
		line-height: 44px;
		border-radius: 3px;
		color:#fff;
		margin-top: 70px;
	}
	.yudingTo:hover{
		background-color: rgb(196, 37, 76);
	}
	#book{
		background-color: #f4f4f4;
		overflow: hidden;
	}
	#book .bookCont{
		width: 935px;
		height: 500px;
		margin: 0 auto;
		margin-top: 50px;
		margin-bottom: 100px;
	}
	#book .bookContLeft{
		float: left;
		width: 680px;
		height: 100%;
		border: 1px solid #ccc;
		background-color: #fff;
		box-sizing: border-box;
		padding: 40px;
	}
	#book .bookContRight{
		float: right;
		width: 240px;
		height: 100%;
		padding: 5px;
		border: 1px solid #ccc;
		background-color: #fff;
		box-sizing: border-box;
	}
</style>



