<template>
	<div class="navBox">
		<div class="nav">
			<div class="logo">
				<img src="https://ccdn.goodq.top/caches/628d236aceaa012698bd7fe4b8dc188c/aHR0cDovLzU4NzQ1YTE5NzJjNmYudDczLnFpZmVpeWUuY29tL3FmeS1jb250ZW50L3VwbG9hZHMvMjAxNy8wNC85YTZhMTc4ZGMyNjAyNTRkZWIwZGQxYzlmODFiOGMzZS5wbmc_p_p100_p_3D.png" alt="">
			</div>
			<ul>
				<li v-for="(i,nav_index) in arr" :class="nav_index==nav_n?'active':''">
					<a :href="'#'+i.path" @click="btn(nav_index)">{{i.title}}<span></span></a>
				</li>
			</ul>
			<div class="loginBox">
				<a href="#/login" @click="btn(7)"><span class="iconfont icon-dengluyonghu" style="font-size: 10px;margin: 0px 2px;"></span>登录</a>
				<a href="#/reg" @click="btn(8)"><span class="iconfont icon-denglulogin10" style="font-size: 10px;margin: 0px 2px;"></span>注册</a>
			</div>
		</div>
	</div>
</template>

<script>
	import Bus from './bus.js'
	var activeN=0;
	Bus.$on('activeNum', (data) => {
		activeN = data;
	})
	export default{
		data(){
			return {
				arr:[
					{title:'首页',path:'/'},
					{title:'热门景点',path:'/hot'},
					{title:'旅游套餐',path:'/set_meal'},
					{title:'关于我们',path:'/about'},
					{title:'客户反馈',path:'/feedback'},
					{title:'联系我们',path:'/contack'},
					{title:'旅游签证',path:'/visa'}
				],
				nav_n:activeN
			}
		},
		methods:{
			btn(n){
				this.nav_n=n;
				Bus.$emit('page', n)
			}
		}
	}
</script>

<style>
	.navBox{
		position: relative;
	}
	.nav{
		position: absolute;
		left: 50%;
		top: 0;
		width: 1180px;
		margin-left: -590px;
		height: 90px;
		box-sizing: border-box;
		padding: 10px 0px;
		z-index: 99;
	}
	.logo img{
		width: 152px;
		height: 60px;
		margin-left: 8px;
		margin-top: 2px;
	}
	.logo{
		float: left;
	}
	.nav ul{
		float: left;
		margin-left: 50px;
		line-height: 80px;
	}
	.nav li{
		float: left;
	}
	.nav li>a{
		color:#fff;
		font-size: 14px;
		margin: 0px 20px;
		position: relative;
	}
	.nav .active span{
		display: block;
		position: absolute;
		left: 0;
		bottom: -10px;
		width: 100%;
		height: .8px;
		background-color: #fff;
	}
	.loginBox{
		float: right;
		line-height: 76px;
	}
	.loginBox a{
		color:#fff;
		font-size: 12px;
		margin-left: 5px;
	}
</style>



