		width: 183px;
		height: 40px;
		text-align: center;
		line-height: 40px;
		border-radius: 5px;
		background-color: #fff;
		font-size: 15px;
		color:rgb(236, 79, 98);
		position: relative;
		z-index: 99;
		margin-top: 20px;
		cursor: pointer;
	}
	.index_b_btn a:hover{
		background-color: rgb(226,226,226);
		transition: all .5s;
	}
	.toBtn{
		display: block;
		width:40px;
		height: 40px;
		border-radius: 50%;
		margin: 0 auto;
		box-sizing: border-box;
		border: 2px solid #fff;
		position: relative;
		z-index: 99;
		margin-top: 120px;
		text-align: center;
		line-height: 40px;
		cursor: pointer;
		transition: .2s;
	}
	.toBtn:hover{
		background-color: rgba(255,255,255,.3);
	}
</style>



