<template>
	<div id="mapBgc">
		<div id="mapBox">
			<div id="map"></div>
			<div style="float: right;width: 255px;">
				<p style="color: #666;">联系方式  / contact</p>
				<p style="font-size: 12px;color:#888;margin-top: 30px;">电话：800-0885-460</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">网址：www.qifeiye.com</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">邮箱：info@email.com</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">地址：苏州市工业园区东方之门</p>
				<p style="color: #666;margin-top: 50px;">营业时间  / business hours</p>
				<p style="font-size: 12px;color:#888;margin-top: 30px;">周一~周五</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">上午：10:00-14:00</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">下午：15:00-20:00</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">周六~周日</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">上午：08:00-14:00</p>
				<p style="font-size: 12px;color:#888;margin-top: 10px;">下午：15:00-19:00</p>
				
				
				
				
				
				
			</div>
			
		</div>
	</div>
</template>

<script>
	import BMap from 'BMap'
	export default{
		mounted() {
			var map = new BMap.Map("map");
			// 创建地图实例  
			var point = new BMap.Point(116.404, 39.915);
			// 创建点坐标  
			map.centerAndZoom(point, 10);
		
			// 初始化地图，设置中心点坐标和地图级别  
			map.enableScrollWheelZoom(true);
			//鼠标滑轮放大缩小
			map.addControl(new BMap.NavigationControl());
		
			map.addControl(new BMap.ScaleControl());
			map.addControl(new BMap.OverviewMapControl());
			map.addControl(new BMap.MapTypeControl());
		}
	}
	</script>
</script>

<style>
	#mapBgc{
		background-color: #fbf8f8;
		overflow: hidden;
	}
	#mapBox{
		width: 1000px;
		height: 400px;
		margin: 30px auto;	
	}
	#map {
		height: 100%;
		width: 700px;
		float: left;
	}
</style>



