import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
import no1 from './1.vue'
import no2 from './2.vue'
import no3 from './3.vue'
import no4 from './4.vue'
import no5 from './5.vue'
import no6 from './6.vue'
import no7 from './7.vue'
import no8 from './8.vue'
import no9 from './9.vue'
import spot from './spot.vue'
import book from './book.vue'
import bar from './bar.vue'
export default new Router({
	routes: [{
			title: '首页',
			path: '/',
			component: no1,
			
		},
		{
			title: '热门景点',
			path: '/hot',
			component: no2
		},
		{
			title: '旅游套餐',
			path: '/set_meal',
			component: no3
		},
		{
			title: '关于我们',
			path: '/about',
			component: no4
		},
		{
			title: '客户反馈',
			path: '/feedback',
			component: no5
		},
		{
			title: '联系我们',
			path: '/contack',
			component: no6
		},
		{
			title: '旅游签证',
			path: '/visa',
			component: no7
		},
		{
			title: '登录',
			path: '/login',
			component: no8
		},
		{
			title: '注册',
			path: '/reg',
			component: no9
		},
		{
			path:'/spots',
			component:spot
		},
		{
			path:'/book',
			component:book
		},
		{
			path:'/bar',
			component:bar
		}
	]
})



