<template>
	<div id="travelBox">
		<div class="processTit">巴黎热门旅游景点</div>
		<div class="processTxt">CITY TOURS / TOUR TICKETS / TOUR GUIDES</div>
		<div class="travelList">
			<div v-for="i in arr">
				<div class="travelListIcon">{{i.icon}}</div>
				<div class="travelListTit"><span style="color:#E04F67;position: relative;top: 2px;">+{{i.num}}</span> <span>{{i.name}}</span></div>
				<div class="travelListTxt">{{i.txt}}</div>
				<a class="travelListBtn">了解更多</a>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				arr:[
					{icon:'1',num:'120',name:'高端旅游',txt:'当今最领先的响应式自助建站平台，我们的流线式网页布局设计方案和可视化图文内容编辑模式让网站制作和维护成为一件轻松惬意的事'},
					{icon:'1',num:'120',name:'高端旅游',txt:'当今最领先的响应式自助建站平台，我们的流线式网页布局设计方案和可视化图文内容编辑模式让网站制作和维护成为一件轻松惬意的事'},
					{icon:'1',num:'120',name:'高端旅游',txt:'当今最领先的响应式自助建站平台，我们的流线式网页布局设计方案和可视化图文内容编辑模式让网站制作和维护成为一件轻松惬意的事'}
				]
			}
		}
	}
</script>

<style>
	.travelListBtn{
		display: block;
		width: 124px;
		height: 28px;
		text-align: center;
		line-height: 27px;
		margin: 0 auto;
		border-radius: 3px;
		background-color: rgb(245, 134, 101);
		box-sizing: border-box;
		border: 1px solid rgb(245, 134, 101);
		font-size: 13px;
		font-weight: bold;
		color:#fff;
		cursor: pointer;
		transition: .5s;
		margin-top: 15px;
	}
	.travelListBtn:hover{
		background-color: #fff;
		color:rgb(245, 134, 101);
	}
	.travelListTit{
		font-size: 20px;
		font-weight: bold;
		margin-top: 20px;
	}
	.travelListTxt{
		margin-top: 22px;
		padding: 0px 20px;
		font-size: 12px;
		color:#777;
		line-height: 25px;
	}
	#travelBox{
		background-color: #f4f4f4;
		height: 620px;
		overflow: hidden;
	}
	.travelList{
		text-align: center;
		margin-top: 50px;
	}
	.travelList>div{
		display: inline-block;
		width: 326px;
		height: 353px;
		border-radius: 10px;
		background-color: #fff;
		margin: 0px 18px;
	}
	.travelListIcon{
		border-radius: 50%;
		margin: 0 auto;
		border: 2px solid #f58665;
		width: 84px;
		height: 84px;
		text-align: center;
		line-height: 84px;
		margin-top: 50px;
	}
</style>



