<template>
	<div id="spot">
			<banner></banner>
			<div class="spotCont">
				<div class="spotContLeft">
					<div class="spotContHead">
						<img :src="data.img" style="width: 343px;height: 200px;" alt="">
						<div class="spotHeadInfo">
							<p>{{data.name}}</p>
							<p style="color: #E04F67;font-size: 20px;font-weight: bold;margin: 6px 0px;">{{data.fNum}}</p>
							<p style="color: #E04F67;font-size: 20px;font-weight: bold;">★★★★★</p>
							<a href="#/book" class="spotYding" @click="changePage">在线预订</a>
						</div>
					</div>
					<div class="introduce" style="border-left: 3px solid #E04F67;margin-top: 30px;text-indent: 7px;font-size: 18px;">景点介绍 <span style="color:#ccc;font-size: 14px;">/ introduce</span></div>
					<div class="introduceTxt" style="margin-left: 10px;font-size: 12px;color: #999;line-height: 22px;margin-top: 30px;">{{data.txt}}</div>
					<div class="introduceTips" style="height: 140px;border: 1px solid #e0798a;background-color: #ffeaeb;margin-top: 20px;box-sizing: border-box;padding: 20px;">
						<p style="font-size: 14px;font-weight: bold;">小贴士</p>
						<p style="font-size: 11px;color: rgb(255, 51, 102);margin: 5px 0px;">快捷游览提示</p>
						<p style="font-size: 11px;color:#666;">{{data.tips}}</p>
					</div>
					<div class="introduceTime" style="text-align: right;font-size: 14px;margin-top: 10px;color: #999;">信息更新时间：<span style="color: #45a7e8;font-size: 12px;">{{data.time}}</span></div>
				</div>
				<div class="spotContRight">
					
				</div>
			</div>
	</div>
</template>

<script>
	import banner from '../parts/banner.vue' 
	import Bus from '../parts/bus.js'
	var d;
	Bus.$on('da',(data)=>{
		d=data;
	})
	export default{
		components:{
			banner
		},
		data(){
			return{
				data:d
			}
		},
		beforeMount(){
			Bus.$on('da',(data)=>{
				this.data=data;
			})
		},
		methods:{
			changePage(){
				Bus.$emit('chanPA', 9)
				Bus.$emit('itemData',this.data)
				// console.log(this.data)
			}
		}
	}
</script>

<style>
	.spotContHead img{
		float: left;
		margin-right: 20px;
	}
	.spotHeadInfo{
		float: left;
	}
	.spotYding{
		display: block;
		background-color: rgb(224, 79, 103);
		width: 168px;
		height: 44px;
		text-align: center;
		line-height: 44px;
		border-radius: 3px;
		color:#fff;
		margin-top: 70px;
	}
	.spotYding:hover{
		background-color: rgb(196, 37, 76);
	}
	.spotContHead{
		height: 247px;
		border-bottom: 1px solid #ccc;
	}
	#spot{
		background-color: #f4f4f4;
		overflow: hidden;
	}
	.spotCont{
		width: 935px;
		height: 800px;
		margin: 0 auto;
		margin-top: 50px;
		margin-bottom: 100px;
	}
	.spotContLeft{
		float: left;
		width: 680px;
		height: 100%;
		border: 1px solid #ccc;
		background-color: #fff;
		box-sizing: border-box;
		padding: 40px;
	}
	.spotContRight{
		float: right;
		width: 240px;
		height: 100%;
		padding: 5px;
		border: 1px solid #ccc;
		background-color: #fff;
		box-sizing: border-box;
	}
</style>



