						}
					}
				}else{
					if(this.menuN==0){
						this.listItem=this.list.slice(0,6);
					}else{
						this.listItem=this.list;
					}
					
				}
			},
			spotsBtn(pag,data){
				Bus.$emit('sss', pag);
				Bus.$emit('da',data);
			}
		}
	}
</script>

<style>
	.hotList{
		padding: 0px 180px;
		text-align: center;
	}
	.hotList>a{
		width: 310px;
		height: 258px;
		display: inline-block;
		margin: 0px 10px;
		margin-bottom: 30px;
		box-shadow: 0px 3px 8px #ccc;
		background-color: #fff;
	}
	.hotList>a>div{
		
	}
	.hotList>a>div:hover{
		color:#999;
	}
	
	.hotImgBox{
		width: 100%;
		height: 179px;
		position: relative;
	}
	.hotMask{
		position: absolute;
		width: 100%;
		height: 100%;
		background-color: rgba(220,220,220,.6);
		text-align: center;
		line-height: 179px;
	}
	.hotList img{
		width: 100%;
		height: 100%;
	}
	.hotInfo{
		box-sizing: border-box;
		padding-top: 17px;
		text-align: center;
	}
	.score{
		margin-top: 6px;
		font-size: 12px;
		color:#999;
	}
	.hotMenu{
		text-align: center;
		margin: 20px 0px;
	}
	.hotMenu a{
		display: inline-block;
		width: 100px;
		height: 34px;
		border: 1px solid #cecece;
		color:#999;
		line-height: 34px;
		font-size: 12px;
		cursor: pointer;
		margin: 0px 3px;
	}
	.hotMenu a:hover{
		border:1px solid rgb(236, 79, 98);
		color:rgb(236, 79, 98);
		background-color: #fff;
	}
	.hotMenu .active{
		border:1px solid rgb(236, 79, 98);
		color:rgb(236, 79, 98);
		background-color: #fff;
	}
	.hotBox{
		overflow: hidden;
	}
	.hot{
		width: 1180px;
		margin: 0 auto;
	}
	.search{
		margin-top: 50px;
		font-weight: bold;
		font-size: 20px;
		color:#555;
		box-sizing: border-box;
		padding: 0px 130px;
		margin-bottom: 100px;
	}
	.searchInp{
		float: right;
		position: relative;
	}
	.searchInp input{
		width: 350px;
		height: 40px;
		border: 1px solid #dbdbdb;
		color: #bfbfbf;
		font-size: 14px;
		text-indent: 8px;
		background-color: #fff;
		box-sizing: border-box;
	}
	.searchBtn{
		position: absolute;
		width: 33px;
		height: 100%;
		right:0;
		top: 0;
		background-color: #dbdbdb;
		text-align: center;
		line-height: 40px;
	}
	.hot_tit,.hot_txt{
		text-align: center;
	}
	.hot_tit{
		font-size: 22px;
		color:#555;
	}
	.hot_txt{
		color:#999;
		margin-top: 10px;
	}
</style>



