<template>
	<div style="border-bottom: 1px solid #ccc;">
		<banner></banner>
		<div :style="'width: 1000px;height:'+((change_arr.length*120)+100)+'px;margin:0 auto;padding-top:30px;'">
			<div class="barLeft">
				<div class="barTit">
					<p style="margin-left: 200px;">商品</p>
					<p style="margin-left: 170px;margin-right: 20px;">价格</p>
					<p>数量</p>
					<p style="float: right;">合计</p>
				</div>
				<div v-if="change_arr.length==0" style="color: #999;">购物车是空的<a href="#/hot" style="font-size: 12px;color:#0074D9;margin-left: 5px;">热门景点</a></div>
				<div v-for="(i,index) in change_arr" style="height: 120px;line-height: 119px;">
					<div style="float: left;position: relative;top:-7px;margin-right: 5px;cursor: pointer;" @click="deleteItem(index)"><span class="iconfont icon-cuo" style="color: red;"></span></div>
					<img :src="i.img" alt="" style="height: 100px;width: 150px;float: left;border-radius: 3px;">
					<p style="float: left;margin-left: 30px;width: 200px;">{{i.name}}</p>
					<p style="float: left;margin-left: 7px;margin-right: 15px;">{{i.money}}</p>
					<button style="width: 20px;float: left;margin-left: 20px;position: relative;top: 50px;outline:none;margin: 0px 2px;border-radius: 3px;" @click="barJian(index)">-</button>
					<p style="float: left;">{{i.itemNNN}}</p>
					<button style="width: 20px;float: left;position: relative;top: 50px;outline:none;margin: 0px 2px;border-radius: 3px;" @click="barJia(index)">+</button>
					<p style="float: right;">¥{{i.itemNNN*i.money}}</p>
				</div>
			</div>
			<div class="barRight">
				
				<div>
					<p style="border-bottom: 2px solid #ccc;padding: 5px 0px;">购物车总计</p>
					<p style="font-size: 14px;color:#999;margin: 10px 0px;">小计：{{moneyCom()}}</p>
				</div>
			</div>
		</div>
		<!-- {{change_arr}} -->
	</div>
</template>

<script>
	import banner from '../parts/banner.vue' 
	import Bus from '../parts/bus.js'
	import {mapGetters,mapActions} from 'vuex'
	// var item;
	var arr=[];
	var change_list=[];
	Bus.$on('pushItem',(data)=>{
		// item=data;
		arr.push(data)
	})
	export default{
		components:{
			banner
		},
		
		data(){
			return{
				change_arr:change_list
			}
		},
		beforeMount(){
			change_list=[];
			for(var i=0;i<arr.length;i++){
				if(change_list.length){
					var when=true;
					for(var j=0;j<change_list.length;j++){
						if(change_list[j].name==arr[i].name){
							change_list[j].itemNNN+=parseInt(arr[i].itemNNN);
							arr.splice(i,1)
							when=false;
						}
					}
					if(when){
						change_list.push(arr[i])
					}
				}else{
					change_list.push(arr[i])
				}
				
			}
			this.change_arr=change_list;
			console.log(change_list)
			
		},
		created(){
			Bus.$on('pushItem',(data)=>{

			})
		},
		methods:{
			moneyCom(){
				var num=0;
				for(var i=0;i<this.change_arr.length;i++){
					num+=this.change_arr[i].money*this.change_arr[i].itemNNN;
				}
				return num;
			},
			barJia(nn){
				var ddd=this.change_arr[nn];
				ddd.itemNNN++;
				this.change_arr.splice(nn,1,ddd)
			},
			barJian(nn){
				var ddd=this.change_arr[nn];
				ddd.itemNNN--;
				if(ddd.itemNNN<=1){
					ddd.itemNNN=1;
				}
				this.change_arr.splice(nn,1,ddd)
			},
			deleteItem(nn){
				this.change_arr.splice(nn,1)
				arr.splice(nn,1)
			}
		}
	}
</script>

<style>
	.barLeft{
		float: left;
		width: 700px;
		box-sizing: border-box;
	}
	.barRight{
		float: right;
		width: 270px;
		box-sizing: border-box;
		padding-left: 30px;
	}
	.barTit{
		border-bottom: 2px solid #ccc;
		margin-bottom: 10px;
		padding: 5px 0px;
	}
	.barTit p{
		display: inline-block;
	}
</style>



